import React from "react"
import PropTypes from "prop-types"
import { MDXRenderer } from "gatsby-plugin-mdx"
import { graphql } from "gatsby"

import Layout from "../Layout"
import SEO from "../SEO"
import PostNav from "./PostNav"

export default function Docs({ data, pageContext }) {
  const { prev, next } = pageContext
  const {
    title,
    description,
    image,
    disableTableOfContents
  } = data.mdx.frontmatter
  const { headings, body } = data.mdx
  const { slug } = data.mdx.fields

  return (
    <>
      <SEO title={title} description={description} slug={slug} image={image} />
      <Layout
        disableTableOfContents={disableTableOfContents}
        title={title}
        headings={headings}
      >
        <MDXRenderer>{body}</MDXRenderer>
        <PostNav prev={prev} next={next} />
      </Layout>
    </>
  )
}

Docs.propTypes = {
  mdx: PropTypes.shape({
    body: PropTypes.string,
    headings: PropTypes.array,
    frontmatter: PropTypes.shape({
      title: PropTypes.string,
      description: PropTypes.string,
      image: PropTypes.string,
      disableTableOfContents: PropTypes.bool
    }),
    fields: PropTypes.shape({
      slug: PropTypes.string
    })
  }).isRequired,
  pageContext: PropTypes.shape({
    prev: PropTypes.shape({}),
    next: PropTypes.shape({}),
    githubEditUrl: PropTypes.string
  }).isRequired
}

export const query = graphql`
  query($slug: String!) {
    mdx(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 160)
      fields {
        slug
      }
      frontmatter {
        title
        description
        image
        disableTableOfContents
      }
      body
      headings {
        depth
        value
      }
    }
  }
`
